body {
  margin: 0;
  color: white;
  background-color: black;
  font-size: calc(10px + 2vmin);
  min-height: 750px;
}
.App {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    display: grid;
}
.titleStyle {
  text-align: center;
  grid-column-start: 1;
  grid-column-end: 4;
}
.blogMain {
  text-align: center;
  grid-column-start: 1;
  grid-column-end: 4;
}
.NavBar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  background-color: gray;
  grid-column-start: 1;
  grid-column-end: 4;
}
.NavItem {
  display: inline;
  float: left;
}

.NavItem:last-child{
  float: right;
}

.NavItemDropContent{
  display: none;
  position: absolute;
  min-width: 160px;
}

.NavItem:hover .NavItemDropContent{
  display: block
}